import React, { Component } from 'react'
import { validateEmail, validPhoneNo, validUserName, validateSpecialRequest } from '../validation/Validation'

class VisitorModel extends Component {

    state = {
        phoneNo: '',
        name: '',
        email: '',
        specialRequest: ''
    }

    handleChange = (e)=>{
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    submitDetails = ()=>{
        if(this.state.email != '' && !validateEmail(this.state.email)) return;
        if(!validPhoneNo(this.state.phoneNo)) return;
        if(!validUserName(this.state.name)) return;
        if(this.state.specialRequest != '' && !validateSpecialRequest(this.state.specialRequest)) return;

        this.props.sendData(this.state);
    }

    render(){
        return (
            <div className="modal" id="user-info">
                <div className="modal-content center">
                    <h6>In Order To Provide You Best Deal Our Team Manually 
                        Search All Over Internet For You</h6>
                    <div className="input-field">
                        <i className="material-icons prefix">account_circle</i> 
                        <input onChange={this.handleChange} id="name" placeholder="Name" type="text" className="validate" />
                    </div>
                    <div className="input-field">
                        <i className="material-icons prefix">email</i> 
                        <input onChange={this.handleChange} id="email" placeholder="Email (Optional)" type="text" className="validate" />
                    </div>
                    <div className="input-field">
                        <i className="material-icons prefix">phone</i>
                        <input onChange={this.handleChange} id="phoneNo" placeholder="Phone No" type="tel" className="validate" />
                    </div>
                    <div className="input-field">
                        <i className="material-icons prefix">mode_edit</i>
                        <textarea onChange={this.handleChange} id="specialRequest" placeholder="Any Special Request ( Optional )" className="materialize-textarea"></textarea>
                    </div>
                    <h6>Fill In Your Details And Our Team Will Reach To You</h6>
                    <div className="modal-footer">
                        <button onClick={this.submitDetails} className="btn blue waves-effect waves-light">Submit</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default VisitorModel;