let validateEmail = (email)=>{
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)){
        return true;
    }
    alert("Invalid Email");
    return false;
}


let validPhoneNo = (phoneNo)=>{
    var phoneno = /^\+?([0-9]{2})\)?[-. ]?([0-9]{10})$/;
    if((phoneNo.match(phoneno))){
        return true;
    }
    alert("Invalid PhoneNo");
    return false;
}

let validUserName = (username)=>{
    var valid = /^[a-z0-9_-]{3,16}$/;
    if(username.toLowerCase().match(valid)) return true;
    alert("Invalid Username")
    return false;
}

let validateSpecialRequest = (data)=>{
    var valid = /^[a-z0-9_-]{3,50}$/;
    if(data.match(valid)) return true;

    alert("Invalid Special Request");
    return false;
}

module.exports.validateEmail = validateEmail;
module.exports.validPhoneNo = validPhoneNo;
module.exports.validUserName = validUserName;
module.exports.validateSpecialRequest = validateSpecialRequest;