import React, { Component } from 'react'
import { Carousel, Autocomplete, Datepicker, Dropdown, Modal, FormSelect } from 'materialize-css/dist/js/materialize.min.js'
import VisitorModel from './VisitorModel'
import axios from 'axios'

let prefixUrl = "/";

let autocompleteData = {
    "Goa": null,
    "Varanasi": null,
    "Mumbai ( Shivaji Maharaj Airport )": null,
    "Manali": null,
    "Modinagar": null,
    "Delhi ( Delhi International Airport )": null
}

class GuideCaro extends Component {

    state = {
        destination: '',
        hireInDate: null,
        hireOutDate: null,
        guide: '',
        typeOfTourist: ''
    }

    componentDidMount(){
        if(window.screen.availWidth > 750){
            const flightCaro = document.querySelector('.guide-caro');
            let instance = Carousel.init(flightCaro,{
                fullWidth: true,
                duration: 400
            })

            setInterval(()=>{
                instance.next()
            },6000)
        }

        var fromCity = document.querySelector('.fromCity');
        Autocomplete.init(fromCity,{
            data: autocompleteData,
            limit: 4,
            onAutocomplete: (val)=>{
                this.setState({
                    destination: val
                })
            }
        })

        var checkInDate = document.querySelector('.checkInDate');
        Datepicker.init(checkInDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    hireInDate: selectedDate
                });
            }
        })

        var checkOutDate = document.querySelector('.checkOutDate');
        Datepicker.init(checkOutDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    hireOutDate: selectedDate
                })
            }
        })

        var eventType = document.querySelectorAll('.event-type');
        FormSelect.init(eventType,{

        })

        var userInfo = document.getElementById('user-info');
        this.userDetailsModel =  Modal.init(userInfo,{
            
        })

        var elems = document.querySelectorAll('.learn-modal');
        Modal.init(elems,{
        })

    }


    handleGuideType = (e)=>{
        this.setState({
            guide: e.target.options[e.target.selectedIndex].value
        })
    }

    handleGroupType = (e)=>{
        this.setState({
            typeOfTourist: e.target.options[e.target.selectedIndex].value
        })
    }    

    submitHotelData = ()=>{

        if(this.state.destination == '' || this.state.guide == '' || this.state.typeOfTourist == ''){
            alert("Please Fill All Details");
            return;
        }

        if(!this.state.hireInDate || !this.state.hireOutDate){
            alert("Please Fill Dates");
            return;
        }

        this.userDetailsModel.open();
    }

    sendAllDataToServer = (userData)=>{
        this.userDetailsModel.close();

        axios.post(`${prefixUrl}services/`,{
            userData: userData,
            data: this.state,
            serviceType: 'Guide'
        }).then((res)=>{
            alert("Thanks For Choosing Us we will contact you")
        }).catch((err)=>{
            console.log(err);
        })

    }

    render(){
        return (
            <div id="booking-activity">
                <div className="carousel carousel-silder default-caro booking-caro guide-caro">
                    <a className="carousel-item" href="#one!">
                        <div className="caro-item" id="caro-1"></div>
                    </a>
                    <a className="carousel-item" href="#two!">
                        <div className="caro-item" id="caro-2"></div>
                    </a>
                    <a className="carousel-item" href="#three!">
                        <div className="caro-item" id="caro-3"></div>
                    </a>  
                </div>

                <div id="flight-model" className="container">
                    <div id="flight-model-content">
                        <div id="flight-iconBox">
                            <i className="material-icons">explore</i>
                            <span>GUIDE</span>
                        </div>

                        <div className="row">
                            <div className="col s12 m9">
                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">location_on</i>
                                        <input type="text" id="form-city-input" className="autocomplete fromCity" placeholder="Location"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker checkInDate" placeholder="Hire In Date"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker checkOutDate" placeholder="Hire Out Date"></input>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <select defaultValue="Default" className="event-type" onChange={this.handleGuideType}>
                                            <option value="Default" disabled >Guide Type</option>
                                            <option value="Tour Coordinator">Tour Coordinator</option>
                                            <option value="Tour Guide">Tour Guide</option>
                                            <option value="Sightseeing Guide">Sightseeing Guide</option>
                                            <option value="Tour Guide">Tour Guide</option>
                                            <option value="Tour Manager">Tour Manager</option>
                                        </select>
                                        <a href="#learn-more" className="modal-trigger">Learn About Tour Guides</a>
                                        <div id="learn-more" className="modal learn-modal">
                                            <div className="modal-content">
                                                <h6>Types Of Guide</h6>
                                                <h5>Tour Coordinator</h5>
                                                <p>Hospitality Professionals Expert at Managing the Group Event/Tour</p>

                                                <h5>Tour Guide</h5>
                                                <p>Tour Guide Accomplish with the group/individual thoughtout the travel duration
                                                    serving proper guidence for right information and ensure safety of Tourist
                                                </p>

                                                <h5>Sightseeing Guide</h5>
                                                <p>Expert Person who knows a particlar Region/Destination/Monument/Pilgrim
                                                    or sight locally more than enough to story the fact behind exsistence
                                                </p>

                                                <h5>Tour Leader</h5>
                                                <p>(A Travel Expert who manages to faciliate all the needful services to a group of tourist
                                                    travelling to a destination, he acts as a tour escort who accompanies a group and 
                                                    ensures proper flow of travel purpose 
                                                </p>

                                                <h5>Tour Manager</h5>
                                                <p>A Manager Personal Engaged in Touring and Handling a Team Professionally 
                                                    and  perfect in all situations
                                                </p>

                                            </div>
                                        </div>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <select  defaultValue="Default" className="event-type" onChange={this.handleGroupType}>
                                            <option value="Default" disabled >Type of Tourist</option>
                                            <option value="Tour Coordinator">Group (9+)</option>
                                            <option value="Tour Guide">Individual (0-9)</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div className="col s12 m2 offset-m1">
                                <button onClick={this.submitHotelData} className="waves-effect waves-light btn blue" id="search">Search</button>
                            </div>
                        </div>
                    </div>
                </div>
                <VisitorModel sendData={this.sendAllDataToServer} />
            </div>
        )
    }
}

export default GuideCaro;